import { Component, OnInit } from '@angular/core';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-bubble',
  templateUrl: './bubble.component.html',
  styleUrls: ['./bubble.component.scss']
})
export class BubbleComponent implements OnInit {

  private isEnglish: boolean = false;
  private baseUrl: string = window.location.href;
  language: string = this.baseUrl.includes('pt-PT') || this.baseUrl.includes('pt') ? "PT" : "EN";

  constructor() { }

  ngOnInit(): void { }

  private getLanguage(isEnglish: boolean): string {
    return isEnglish ? "EN" : "PT";
  }

  changeLanguageOnClick(): void {
    this.isEnglish = !this.isEnglish;
    this.language = this.getLanguage(this.isEnglish);

    window.location.href = "https://namelesssoftworks.github.io/demo/acorbiz/" + this.isEnglish ? 'en-US' : 'pt-PT';
  }
}
